body {
  margin: 0;
}

/* 滚动条 - 全局 */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
#dataBox::-webkit-scrollbar {
  width: 0px;
}

/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #DEDFE1;
}
/* 滚动条轨道 */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background: transparent;
}